@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');


@layer components {
    .btn {
      @apply py-2 px-4 bg-primary-blue text-white rounded-lg w-fit my-2 dark:bg-secondary-blue dark:text-dark-bg font-semibold;
    }
  }

  .svg-light-filter {
    filter: invert(31%) sepia(42%) saturate(861%) hue-rotate(148deg) brightness(95%) contrast(95%);
    }

  .svg-dark-filter {
    filter: invert(76%) sepia(50%) saturate(2632%) hue-rotate(143deg) brightness(105%) contrast(104%);
  }